<template>
    <bda-dialog
        :visible="dialogVisible"
        :modal="true"
        :show-close="false"
        :close-on-click-modal="false"
        @cancel="closeDialog"
        title="历史文件及版本"
    >
        <div class="dialogBox">
            <el-upload
                drag
                class="upload-demo"
                :data="fileData"
                :name="fileName"
                :action="fileUrl"
                :on-change="handleChange"
                :before-upload="beforeAvatarUpload"
                :on-success="handleAvatarSuccess"
                :file-list="fileList">
                <i class="el-icon-upload"></i>
                <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
<!--                <div class="el-upload__tip" slot="tip">只能上传jpg/png文件，且不超过500kb</div>-->
<!--                <el-button size="small" type="primary">点击上传</el-button>-->
            </el-upload>
        </div>
        <span slot="footer" class="dialog-footer">
          <bda-button bdatype="defualt" @click="closeDialog">关闭</bda-button>
        </span>
    </bda-dialog>
</template>

<script>
  import Interface from "../../base/mixin/enterprise-manage-center";
  import {
    API_PATH
  } from '@/config'
  export default {
    name: "historyDoc",
    mixins: [Interface],
    props: {
      url: {
        type: String,
        default: () => {
          return ''
        }
      },
      fileName: {
        type: String,
        default: () => {
          return 'files'
        }
      },
      fileData: {
        type: Object,
        default: () => {
          return null
        }
      },
    },
    data(){
      return {
        dialogVisible:false,
        fileUrl:null,
        fileList:[]
      }
    },
    mounted() {
      this.fileUrl = API_PATH + this.url
    },
    methods:{
      /**
       * @description:关闭弹框回调
       */
      closeDialog() {
        this.dialogVisible = false;
        this.$parent.showFileUp = false;
      },

      //文件上传成功钩子
      handleAvatarSuccess(res) {
        if (res){
          this.$message.success('上传成功')
          this.$emit('upLoadSuccess',res)
        }
      },
      //文件上传之前钩子
      beforeAvatarUpload() {
        return true
      },
      handleChange(file, fileList) {
        this.fileList = fileList.slice(-1);
      },
    }
  }
</script>

<style scoped lang="scss">
    ::v-deep .upload-demo{
        .el-upload-dragger {
            background-color: #002a70!important;
        }
    }
    .fileUpload {

    }
    /deep/{
        .el-dialog__wrapper{
            background-color: rgba(0,0,0,0.4)
        }
        /*修改添加规则弹窗 */
        .el-dialog {
            width: 1094px;
            height: 540px;
            background: #1f2c4d;
            border: 1px solid #4389f9;
            border-radius: 4px;
            .el-dialog__header {
                border-bottom: 1px solid #4389f9 !important;
                height: 48px !important;

                .bda-dialog-header .bda-dialog-header-title {
                    font-family: MicrosoftYaHei-Bold;
                    font-size: 16px !important;
                    color: #ffffff !important;
                    line-height: 48px;
                    font-weight: 700;
                }
            }

            .bda-dialog-body-main {
                height: 438px !important;

                .el-form {
                    .el-row:nth-of-type(3) {
                        height: 80px;
                        .el-textarea__inner {
                            height: 60px;
                            background: #041338;
                            border: 1px solid #4389f9;
                            color: #4298f3;
                            &::placeholder {
                                font-family: MicrosoftYaHei;
                                font-size: 14px;
                                color: #4298f3;
                                font-weight: 400;
                            }
                        }
                    }

                    .el-row:nth-of-type(4) {
                        height: 110px;
                        .el-textarea__inner {
                            height: 90px;
                            background: #041338;
                            border: 1px solid #4389f9;
                            color: #4298f3;
                            &::placeholder {
                                font-family: MicrosoftYaHei;
                                font-size: 14px;
                                color: #4298f3;
                                font-weight: 400;
                            }
                        }
                    }
                }
            }

            .el-dialog__footer {
                border-top: 1px solid #4389f9 !important;
                height: 52px !important;

                .dialog-footer {
                    display: block;
                    height: 100%;
                    padding: 11px 20px;
                    box-sizing: border-box;
                }
            }
        }
    }

</style>