<template>
    <div class="blackUserInfo">
        <div class="nav">
<!--            <el-button type="primary" size="mini" @click="uploadVs">导入</el-button>-->
            <el-button type="primary" size="mini" @click="delList">标记</el-button>
<!--            <bda-button @click="downloadTemplate">下载模板</bda-button>-->
            <div style="display: inline-block;float: right;margin-right: 10px">
                <bda-input
                    style="width: 200px;margin-right: 4px"
                    v-model="keyWords"
                    clearable
                    placeholder="请输入关键字查询"
                >
                </bda-input>
                <bda-button @click="getDatas">查询</bda-button>
            </div>
        </div>
        <div style="height: calc(100% - 40px)">
            <element-bda-table-page
                v-loading="dsLoading"
                element-loading-text="拼命加载中"
                element-loading-spinner="el-icon-loading"
                element-loading-background="rgba(0, 0, 0, 0.8)"
                :selection="true"
                :tableData="majorArray"
                :columnArray="majorCol"
                :pageSize = pagesRs.pageSize
                :pageNo = pagesRs.pageNo
                :total = pagesRs.total
                @selectBox = "getSelectRow"
                @getRow="getMajorRow"
                @getPageNo="changePageNo"
                @getPageSize="changePageSize">
            </element-bda-table-page>
        </div>
        <file-upload
            :url="'/black/import'"
            ref="fileUpload"
            :fileName="'excelFile'"
            v-if="showFileUp"
            @upLoadSuccess="fileUpSuccess"
        ></file-upload>
    </div>
</template>

<script>
  import elementBdaTablePage from '@/components/table/element-bda-table-page'
  import Interface from "../../../../../base/mixin/enterprise-manage-center";
  import fileUpload from "../../../../../components/table/fileUpload";
  import {API_PATH} from "@/config"
  export default {
    name:'blackUserInfo',
    mixins: [Interface],
    components:{
      elementBdaTablePage,
      fileUpload
    },
    props: [],
    data() {
      return {
        majorArray: [],
        majorCol:[],
        dsLoading:false,
        pagesRs:{
          pageNo: 1,
          pageSize: 10,
          total: 0,
        },
        selectRows:[],
        //弹窗
        showFileUp:false,
        keyWords:'',

      };
    },
    mounted() {
      this.getDatas()

    },
    created() {

    },
    watch: {

    },
    methods: {
      //导入成功钩子  参数res
      fileUpSuccess(){
        this.getDatas()
      },
      //上传最新版本
      uploadVs(){
        this.showFileUp = true
        this.$nextTick(()=>{
          this.$refs.fileUpload.dialogVisible = true
        })
      },
      getMajorRow(v,op){
        window.console.log(v,op)
      },
      changePageNo(pageNo){
        this.pagesRs.pageNo = pageNo
        this.getDatas()
      },
      changePageSize(size){
        this.pagesRs.pageSize = size
        this.getDatas()
      },

      getDatas(){
        this.dsLoading = true
        this.majorCol=[
          {prop: 'yhbh', label: '用户编号'},
          {prop: 'yhmc', label: '用户名称',},
          {prop: 'yddz', label: '用电地址'},
          {prop: 'updateTime', label: '更新时间'},
          {prop: 'markTime', label: '标记时间'},
          {prop: 'operator', label: '操作人'},
          {prop: 'isBlackUser', label: '是否黑名单用户'},]
        this.blackUserList({
          current:this.pagesRs.pageNo,
          size:this.pagesRs.pageSize,
          "keyword": this.keyWords,
        }).then((res)=>{
          this.pagesRs.total = res.records
          this.majorArray = res.rows.filter(v=>{
            return v.isBlackUser =  v.isBlackUser===1?'是':'否'
          })
        }).finally(()=>{
          this.dsLoading = false
        })
      },

      //复选框改变回调，返回选中数组
      getSelectRow(v){
        this.selectRows = v || []
      },
      //批量标记
      delList(){
        if (this.selectRows.length==0){
          this.$message.warning('请选择需标记的数据')
          return
        }
        let selectIds = []
        selectIds = this.selectRows.map(v=>{
          return v.id
        })
        let params = new URLSearchParams()
        for (let i=0;i < selectIds.length;i++){
          params.append('ids',selectIds[i])
        }
        this.blackUserMark(params).then(()=>{
          this.getDatas()
        })
      },
      //下载模板
      downloadTemplate(){
        window.location.href = `${API_PATH}/dictionary/template?exportMark=blacklist`
      },
    },
  };
</script>

<style lang="scss" scoped>
    .blackUserInfo {
        width: 100%;
        height: 100%;
        box-sizing: border-box;
        color: #ffffff;
        .nav{
            height: 30px;
            line-height: 30px;
            margin-bottom: 6px;
        }
        /deep/ {
            /*修改添加规则弹窗 */
            .el-dialog {
                width: 1094px;
                height: 540px;
                background: #1f2c4d;
                border: 1px solid #4389f9;
                border-radius: 4px;

                .el-dialog__header {
                    border-bottom: 1px solid #4389f9 !important;
                    height: 48px !important;

                    .bda-dialog-header .bda-dialog-header-title {
                        font-family: MicrosoftYaHei-Bold;
                        font-size: 16px !important;
                        color: #ffffff !important;
                        line-height: 48px;
                        font-weight: 700;
                    }
                }

                .dialogBox{
                    color: #ffffff;
                    .jdName{
                        width: 160px;
                        display: inline-block;
                        text-align: right;
                        box-sizing: border-box;
                        padding-right: 20px;
                    }
                    .inputJd{
                        width: calc(100% - 240px);
                    }
                    .jdBtn{
                        width: 80px;
                    }
                }
                .bda-dialog-body-main {
                    height: 438px !important;

                    .el-form {
                        .el-form-item__label{
                            color: #ffffff;
                        }
                        .el-row:nth-of-type(3) {
                            height: 80px;
                            .el-textarea__inner {
                                height: 60px;
                                background: #041338;
                                border: 1px solid #4389f9;
                                color: #4298f3;
                                &::placeholder {
                                    font-family: MicrosoftYaHei;
                                    font-size: 14px;
                                    color: #4298f3;
                                    font-weight: 400;
                                }
                            }
                        }

                        .el-row:nth-of-type(4) {
                            height: 110px;
                            .el-textarea__inner {
                                height: 90px;
                                background: #041338;
                                border: 1px solid #4389f9;
                                color: #4298f3;
                                &::placeholder {
                                    font-family: MicrosoftYaHei;
                                    font-size: 14px;
                                    color: #4298f3;
                                    font-weight: 400;
                                }
                            }
                        }
                    }
                }

                .el-dialog__footer {
                    border-top: 1px solid #4389f9 !important;
                    height: 52px !important;

                    .dialog-footer {
                        display: block;
                        height: 100%;
                        padding: 11px 20px;
                        box-sizing: border-box;
                    }
                }
            }
        }
    }
</style>