<template>
    <div class="element-bda-table" :class="$root.themeHomeChange === '1' ? 'element-bda-tableL' : 'element-bda-table'">
        <bda-scroll-table
            :data="tableData"
            border
            @selection-change="handleSelectionChange"
            :height="tableHeight?tableHeight:null"
            style="width: 100%">
            <el-table-column
                type="selection"
                v-if="selection"
                width="55">
            </el-table-column>
            <el-table-column
                type="index"
                label="序号"
                v-if="index"
                header-align="center"
                align="center"
                width="80">
            </el-table-column>
            <el-table-column
                v-for="item in columnArray"
                show-overflow-tooltip
                :key="item.prop"
                :prop="item.prop"
                :label="item.label"
                :width="item.width">
                <template slot-scope="scope">
                    <span v-if="item.type==='operate'">
                        <el-button type="text"
                                   v-for="item2 in item.linkName"
                                   :key="item2"
                                   @click="rowClick(scope.row,item2)"
                        >{{item2||''}}</el-button>
                    </span>
                    <template v-else-if="item.type==='link'">
                        <div v-for="item2 in scope.row[item.prop]"
                             :key="item2[item.key]||''">
                            <el-button type="text"
                                       @click="handleLink(scope.row,item2)"
                            >{{item2.fileName||''}}</el-button>
                        </div>
                    </template>
                    <span v-else>
                        {{scope.row[item.prop]}}
                    </span>
                </template>
            </el-table-column>
        </bda-scroll-table>
        <div style="text-align: center;margin-top: 8px">
            <el-pagination
                @size-change="handleSizeChangeRs"
                @current-change="handleCurrentChangeRs"
                :current-page="pagesRs.pageNo"
                :page-sizes="[10, 20, 30, 40]"
                :page-size="pagesRs.pageSize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="pagesRs.total">
            </el-pagination>
        </div>
    </div>
</template>

<script>
  export default {
    name: "element-bda-table",
    components:{

    },
    props:{
      tableData:{  //表格数据
        type:Array,
        default:()=>{
          return []
        }
      },
      columnArray:{  //表头
        type:Array,
        default:()=>{
          return []
        }
      },
      pageSize:{
        type:Number,
        default:()=>{
          return 10
        }
      },
      pageNo:{
        type:Number,
        default:()=>{
          return 1
        }
      },
      total:{
        type:Number,
        default:()=>{
          return 0
        }
      },
      tableHeight:{
        type:Number,
        default:()=>{
          return null
        }
      },
      selection:{//复选框
        type:Boolean,
        default:()=>{
          return false
        }
      },
      index:{  //序号
        type:Boolean,
        default:()=>{
          return true
        }
      }
    },
    watch:{
      tableData: {
        handler(newVale) {
          this.tableData = newVale || []
        },
        deep: true, //true 深度监听
        immediate: true //开启首次监听赋值（不开启首次监听不到）
      },
      pageSize(newVale){
        this.pagesRs.pageSize = newVale || 10
      },
      pageNo(newVale){
        this.pagesRs.pageNo = newVale || 1
      },
      total(newVale){
        this.pagesRs.total = newVale || 0
      },
    },
    data(){
      return {
        pagesRs: {
          pageNo: 1,
          pageSize: 10,
          total: 0,
        },
      }
    },
    created() {
    },
    methods:{
      //当有操作列时、点击向外触发点击行数据
      rowClick(row,op){
        if (op==='删除'){
          this.$confirm('此操作将永久删除该数据, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            this.$emit('getRow',row,op)
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除'
            });
          });
        }else {
          this.$emit('getRow',row,op)
        }
      },
      handleLink(row,op){
        this.$emit('linkRow',row,op)
      },
      /**getPageNo当前页方法**/
      handleCurrentChangeRs(val) {
        this.pagesRs.pageNo = val
        this.$emit('getPageNo',val)
      },
      /**getPageSize总页数方法**/
      handleSizeChangeRs(val) {
        this.pagesRs.pageSize = val
        this.$emit('getPageSize',val)
      },
      //复选框选中值
      handleSelectionChange(v){
        this.$emit('selectBox',v)
      },
    }
  }
</script>

<style scoped lang="scss">
    ::v-deep .bda-main .bda-table{
        overflow-y: scroll !important;
        .el-table{
            top: 0!important;
        }
        .el-table:before{
            width: 0;
        }
        .el-table--border:after{
            width: 0;
        }
    }
    .element-bda-table{
        height: 100%;
        width: 100%;
        /deep/ {
            .button-container {
                margin-bottom: 10px;
            }

            /*修改表格单元格高度*/
            .bda-pagination-table {
                .bda-pagination-table-page {
                    border: 0 !important;
                    height: 50px !important;
                }
                .bda-pagination-table-main {
                    height: calc(100% - 40px);
                }
            }

            /*checkbox样式*/

            .el-checkbox__input.is-disabled {
                width: 100%;
                padding: 0 auto;
            }

            .el-checkbox__input.is-disabled .el-checkbox__inner,
            .el-checkbox__input .el-checkbox__inner {
                background-color: #041338 !important;
                width: 16px;
                height: 16px;
                border: 1px solid #4389f9;
                border-radius: 2px;
            }

            /*解决调整单元格高度后，fixed错位*/
            .el-table__fixed-body-wrapper {
                top: 37px !important;
            }

            /*解决表格滚动高度固定导致的问题*/
            .el-table__body-wrapper.is-scrolling-left {
                height: calc(100% - 48px) !important;
            }
            .el-table__body-wrapper.is-scrolling-right {
                height: calc(100% - 48px) !important;
            }
            .el-table__body-wrapper.is-scrolling-middle {
                height: calc(100% - 48px) !important;
            }
            .el-table__fixed-body-wrapper {
                height: calc(100% - 34px) !important;
            }

            /*解决调整列宽度时，出现列内容未完整显示的问题*/
            .el-table .cell.el-tooltip {
                width: 100% !important;
            }

            .el-table__header-wrapper {
                background-color: #22519c;
            }

            .el-table__body-wrapper {
                background-color: #071a47;
            }

            .el-table__header th {
                background-color: #22519c !important;
                height: 48px;
                border-color: #000000;

                .cell {
                    color: #fff !important;
                    text-align: center;
                }
            }

            .bda-table-main {
                background-color: #071a47;
            }

            .bda-main {
                height: calc(100% - 68px);
                border: 1px solid #000000;
                .el-table__row {
                    &:hover > td {
                        background-color: #284385 !important;
                    }
                    > td {
                        height: 48px;
                        border-color: #000000;

                        .cell {
                            color: #fff;
                            text-align: center;

                            .el-button {
                                border-width: 0px !important;
                                background-color: transparent;
                                color: #66b9ff;
                            }
                        }
                    }
                }
                .el-table__row:nth-of-type(odd) {
                    background-color: #223a75;
                }
                .el-table__row:nth-of-type(even) {
                    background-color: #1a3066;
                }

                // 修改 待修定数据 行数样式
                .revision-row {
                    background-color: #b8c228 !important;
                }
            }

            // 修改分页组件样式
            .el-pagination {
                margin-top: 20px;
                text-align: center;
                color: #fff;
                .btn-next,
                .btn-prev,
                .el-pager li,
                .el-select .el-input .el-input__inner,
                .el-pagination__editor.el-input .el-input__inner {
                    background-color: #041338;
                    color: #d3d3d3;
                    border: 1px solid #2a5cb0;
                    border-radius: 3px;
                }
                .el-pager li:not(.disabled).active {
                    background-color: #4389f9;
                }
                .el-pagination__jump,
                .el-pagination__total {
                    color: #fff;
                    margin-left: 10px;
                }
            }

            // 修改input框样式
            .el-input__inner {
                background: #041338;
                color: #4298f3;
                border: 1px solid #4389f9;
            }
            &.el-input__inner::placeholder {
                color: #4298f3;
            }
            .el-select__caret {
                color: #4298f3;
            }

            .el-form-item__label {
                color: #fff;
            }
        }
    }
    .element-bda-tableL{
        /deep/ {
            .el-checkbox__inner:after{
                border-color: #333;
            }
            .button-container {
                margin-bottom: 10px;
            }

            /*修改表格单元格高度*/
            .bda-pagination-table {
                .bda-pagination-table-page {
                    border: 0 !important;
                    height: 50px !important;
                }
                .bda-pagination-table-main {
                    height: calc(100% - 40px);
                }
            }
            .el-form-item__label {
                color: #7f7f7f;
            }
            /*checkbox样式*/

            .el-checkbox__input.is-disabled {
                width: 100%;
                padding: 0 auto;
            }

            .el-checkbox__input.is-disabled .el-checkbox__inner,
            .el-checkbox__input .el-checkbox__inner {
                background-color: #fff !important;
                width: 16px;
                height: 16px;
                border: 1px solid #bbb;
                border-radius: 2px;
            }

            /*解决调整单元格高度后，fixed错位*/
            .el-table__fixed-body-wrapper {
                top: 37px !important;
            }

            /*解决表格滚动高度固定导致的问题*/
            .el-table__body-wrapper.is-scrolling-left {
                height: calc(100% - 48px) !important;
            }
            .el-table__body-wrapper.is-scrolling-right {
                height: calc(100% - 48px) !important;
            }
            .el-table__body-wrapper.is-scrolling-middle {
                height: calc(100% - 48px) !important;
            }
            .el-table__fixed-body-wrapper {
                height: calc(100% - 34px) !important;
            }

            /*解决调整列宽度时，出现列内容未完整显示的问题*/
            .el-table .cell.el-tooltip {
                width: 100% !important;
            }

            .el-table__header-wrapper {
                background-color: #22519c;
            }

            .el-table__body-wrapper {
                background-color: #fff;
            }

            .el-table__header th {
                // background-color: #22519c !important;
                background: rgba(188, 228, 249, 0.4)!important;
                height: 48px;
                border-color: #bbb;

                .cell {
                    color: #666 !important;
                    text-align: center;
                }
            }

            .bda-table-main {
                background-color: #ffffff;
            }

            .bda-main {
                /*height: calc(100% - 52px);*/
                border: 1px solid #bbb;
                .el-table__row {
                    &:hover > td {
                        background-color: #ebfcff !important;
                    }
                    > td {
                        height: 48px;
                        border-color: #bbb;

                        .cell {
                            color: #333;
                            text-align: center;
                            .el-button {
                                color: #66b9ff;
                            }
                        }
                    }
                    > td:nth-last-child(3) {
                        .cell {
                            /*color: red;*/
                            text-align: center;
                        }
                    }
                }
                .el-table__row:nth-of-type(odd) {
                    background-color: #fff;
                }
                .el-table__row:nth-of-type(even) {
                    background-color: #fff;
                }
            }

            // 修改分页组件样式
            .el-pagination {
                margin-top: 20px;
                text-align: center;
                color: #fff;
                .btn-next,
                .btn-prev,
                .el-pager li,
                .el-select .el-input .el-input__inner,
                .el-pagination__editor.el-input .el-input__inner {
                    background: rgba(67,137,249,0.06);
                    color: #333333;
                    border: 0px solid #2a5cb0;
                    border-radius: 4px;
                }
                .el-pager li:not(.disabled).active {
                    background-color: #4389f9;
                }
                .el-pager .active{color: #ffffff}
                .el-pagination__jump,
                .el-pagination__total {
                    color: #545454;
                    margin-left: 10px;
                }
            }
        }
    }
</style>